/**
 * @file
 *
 * Verify by Invoice ID and DOB form.
 *
 * example:
 * <InvoiceForm/>
 */

import React, { useState } from 'react'
import ReCAPTCHA from 'react-google-recaptcha-enterprise'
import { useNavigate } from 'react-router-dom'

import { RECAPTCHA_CLIENT_KEY } from '../../config/config'
import useAuthenticateContext from '../../hooks/useAuthenticate'
import { AuthenticateResponse } from '../../service/authenticate'
import Input from '../form-elements/Input'
import InputDate from '../form-elements/InputDate'
import Submit from '../form-elements/Submit'

interface IDForm {
  invoiceId: string
  dob: string
}

const InvoiceForm: React.FC = () => {
  const { loading, recaptchaToken, setParams, setRecaptchaToken } = useAuthenticateContext()

  const initialValues = { invoiceId: '', dob: '' }
  const [formValues, setFormValues] = useState(initialValues)
  const [formErrors, setFormErrors] = useState(initialValues)
  let isSubmit = false

  const navigate = useNavigate()
  const [message, setMessage] = React.useState('')

  const handleOnChange = (e: any) => {
    const { name, value } = e.currentTarget
    setFormValues({ ...formValues, [name]: value })
  }

  const validate = (values: IDForm) => {
    isSubmit = false
    const errors: IDForm = { invoiceId: '', dob: '' }

    if (values.invoiceId.length < 5) {
      errors.invoiceId = 'Invoice ID is too short'
    } else {
      isSubmit = true
    }
    return errors
  }

  const onSubmit = (e: React.SyntheticEvent) => {
    e.preventDefault()
    setFormErrors(validate(formValues))

    // Get Values from the form.
    const target = e.target as typeof e.target & {
      invoiceId: { value: string }
      dob: { value: string }
    }
    const invoiceId = target.invoiceId.value
    const dob = target.dob.value

    const onSubmitted = (param: AuthenticateResponse) => {
      if (param.member) {
        // Member can be null.
        navigate('/payment', {
          state: {
            balance: param.member.balance,
            id: param.member.id,
            mdvipId: param.member.mdvipId,
            dob: dob,
            token: param.token,
          },
        })
      } else {
        setMessage(param + '' || 'Unknown Error')
      }
    }

    if (isSubmit) {
      // Send values to API.
      setParams({
        invoiceId,
        dob,
        onSubmitted,
      })
    }
  }

  const onRecaptchaChange = (recaptchaToken: string) => {
    setRecaptchaToken(recaptchaToken)
  }

  return (
    <form onSubmit={onSubmit}>
      <Input
        name="invoiceId"
        value={formValues.invoiceId}
        className={formErrors.invoiceId ? 'element-error js-filled' : ''}
        required={true}
        label="Invoice #"
        type="text"
        onChange={handleOnChange}
      />
      <InputDate name="dob" value={formValues.dob} required={true} label="Date of birth" onChange={handleOnChange} />
      <div className="error">
        {message} {formErrors.invoiceId}
      </div>
      <ReCAPTCHA sitekey={RECAPTCHA_CLIENT_KEY} onChange={onRecaptchaChange} />
      <Submit id="mdvip-id-submit" value="Verify Account" disabled={loading || !recaptchaToken} />
    </form>
  )
}

export default InvoiceForm
