/**
 * @file
 *
 * Form Input Date component. Used in Forms to render input date element.
 * Example: <InputDate name="mdvip-dob" label="Date of birth"/>
 */
import React, { ChangeEventHandler, FC, InputHTMLAttributes } from 'react'

interface InputProps extends InputHTMLAttributes<HTMLInputElement> {
  name: string | undefined
  label: string
  required?: boolean | undefined
  onChange?: ChangeEventHandler | undefined
}

const InputDate: FC<InputProps> = (props) => {
  const { name, label, required, onChange } = props
  const dateObject = new Date()
  const currentYear = dateObject.getFullYear()
  // DOB is limited from 18 years old to 110 years old range.
  const yearMax = currentYear - 18
  const yearMin = currentYear - 110
  const day = dateObject.getUTCDate()
  let month: string | number = dateObject.getMonth() + 1

  // Add leading zero to the month's label
  month = month < 10 ? '0' + month : month
  return (
    <div className="text-field--wrapper">
      {/* Toggle js-filled class on click and blur to animate the label up and down. */}
      <input
        id={name}
        name={name}
        type="date"
        placeholder=""
        onBlur={(e) => {
          e.currentTarget.className = ''

          if (e.currentTarget.value.length !== 0) {
            e.currentTarget.className = 'js-filled'
          }
        }}
        className="form-date"
        max={yearMax + '-' + month + '-' + day}
        min={yearMin + '-' + month + '-' + day}
        required={required}
        defaultValue=""
        onChange={onChange}
      />
      <label htmlFor={name}>{label}</label>
    </div>
  )
}

export default InputDate
