/**
 * @file
 *
 * Verify by Phone or E-mail and ZIP and DOB form.
 * Used in "Other" option on to Verify Account Page
 *
 * example:
 * <OtherForm/>
 */
import React, { useState } from 'react'
import ReCAPTCHA from 'react-google-recaptcha-enterprise'
import { useNavigate } from 'react-router-dom'

import { RECAPTCHA_CLIENT_KEY } from '../../config/config'
import useAuthenticateContext from '../../hooks/useAuthenticate'
import { AuthenticateResponse } from '../../service/authenticate'
import Input from '../form-elements/Input'
import InputDate from '../form-elements/InputDate'
import Submit from '../form-elements/Submit'

interface OtherForm {
  phone: string
  dob: string
  zipCode: string
}

const OtherForm: React.FC = () => {
  const { loading, recaptchaToken, setParams, setRecaptchaToken } = useAuthenticateContext()
  let isSubmit = false
  const navigate = useNavigate()
  const [message, setMessage] = React.useState('')
  const initialValues = { phone: '', dob: '', zipCode: '' }
  const [formValues, setFormValues] = useState(initialValues)
  const [formErrors, setFormErrors] = useState(initialValues)

  const onSubmit = (e: React.SyntheticEvent) => {
    e.preventDefault()
    setFormErrors(validate(formValues))
    // Get Values from the form.
    const target = e.target as typeof e.target & {
      phone: { value: string }
      zipCode: { value: string }
      dob: { value: string }
    }
    const zipCode = target.zipCode.value
    const phone = target.phone.value
    const dob = target.dob.value

    const onSubmitted = (param: AuthenticateResponse) => {
      if (param.member) {
        // Member can be null.
        navigate('/payment', {
          state: {
            balance: param.member.balance,
            id: param.member.id,
            mdvipId: param.member.mdvipId,
            dob: dob,
            token: param.token,
          },
        })
      } else {
        setMessage(param + '' || 'Unknown Error')
      }
    }

    // Send values to API.
    if (isSubmit) {
      setParams({
        phone,
        zipCode,
        dob,
        onSubmitted,
      })
    }
  }

  const handleOnChange = (e: any) => {
    const { name, value } = e.currentTarget
    setFormValues({ ...formValues, [name]: value })
  }

  const validate = (values: OtherForm) => {
    isSubmit = true
    const errors: OtherForm = { phone: '', dob: '', zipCode: '' }
    const zipPattern = /(^\d{5}$)|(^\d{5}-\d{4}$)/
    const phonePattern = /^[+]?[(]?[0-9]{3}[)]?[-\s.]?[0-9]{3}[-\s.]?[0-9]{4,6}$/im
    if (!phonePattern.test(values.phone)) {
      errors.phone = 'Phone number is in invalid format.'
      isSubmit = false
    }
    if (!zipPattern.test(values.zipCode)) {
      errors.zipCode = 'Zip code format is invalid.'
      isSubmit = false
    }
    return errors
  }

  const onRecaptchaChange = (recaptchaToken: string) => {
    setRecaptchaToken(recaptchaToken)
  }

  return (
    <form onSubmit={onSubmit}>
      <Input
        name="phone"
        required={true}
        value={formValues.phone}
        className={formErrors.phone ? 'element-error js-filled' : ''}
        label="Phone"
        type="text"
        onChange={handleOnChange}
      />
      <Input
        name="zipCode"
        value={formValues.zipCode}
        className={formErrors.zipCode ? 'element-error js-filled' : ''}
        required={true}
        pattern="[0-9]*"
        label="Zip Code"
        type="text"
        onChange={handleOnChange}
      />
      <InputDate
        name="dob"
        value={formValues.dob}
        className={formErrors.dob ? 'element-error js-filled' : ''}
        required={true}
        label="Date of birth"
        onChange={handleOnChange}
      />
      <div className="error">
        {message} {formErrors.phone} {formErrors.zipCode} {formErrors.dob}
      </div>
      <ReCAPTCHA sitekey={RECAPTCHA_CLIENT_KEY} onChange={onRecaptchaChange} />
      <Submit id="mdvip-id-submit" value="Verify Account" disabled={loading || !recaptchaToken} />
    </form>
  )
}

export default OtherForm
