/**
 * @file
 *
 * Form Input component. Used in Forms to render input element.
 * Example: <Input name="mdvip-id" label="MDVIP ID" type="text"/>
 */
import React, { ChangeEventHandler, FC, FocusEventHandler, InputHTMLAttributes } from 'react'

interface InputProps extends InputHTMLAttributes<HTMLInputElement> {
  name: string
  label: string
  type: string
  onBlur?: FocusEventHandler | undefined
  value?: string
  className?: string
  required?: boolean | undefined
  onChange?: ChangeEventHandler | undefined
}

const Input: FC<InputProps> = (props) => {
  const { name, label, type, onBlur, value, className, required, onChange } = props
  const onBlurHandler = (e: React.FocusEvent<HTMLInputElement>) => {
    e.currentTarget.className = ''
    if (onBlur !== undefined) {
      onBlur(e)
    }

    if (e.currentTarget.value !== '' || e.target.value !== '') {
      e.currentTarget.className = className !== undefined ? className + ' js-filled' : 'js-filled'
    }
  }

  const cssClass = className !== undefined ? 'form-' + type + ' ' + className : 'form-' + type

  return (
    <div className="text-field--wrapper">
      {/* Toggle js-filled class on click and blur to animate the label up and down. */}
      <input
        id={name}
        name={name}
        type={type}
        placeholder=""
        required={required}
        onBlur={onBlurHandler}
        className={cssClass}
        defaultValue={value}
        onChange={onChange}
      />
      <label htmlFor={name}>{label}</label>
    </div>
  )
}

export default Input
