/**
 * @file
 *
 * Pay Form. Used on Add Payment Info page.
 *
 * example:
 * <PayForm/>
 */
import 'react-tabs/style/react-tabs.css'

import { useMutation } from '@apollo/client'
import CheckIcon from '@mui/icons-material/Check'
import { card, cvc } from 'creditcards'
import expiration from 'creditcards/expiration'
import React, { FocusEvent, useState } from 'react'
import { useLocation, useNavigate } from 'react-router-dom'
import Select from 'react-select'
import { Tab, TabList, TabPanel, Tabs } from 'react-tabs'
import { accountNumber, routingNumber } from 'us-bank-account-validator'

import { MDVIP_LOGIN_LINK, MONTHS, MUTATION_ACH, MUTATION_CREDIT } from '../../Constants'
import Option from '../../Constants'
import Input from '../form-elements/Input'
import Radio from '../form-elements/Radio'
import Submit from '../form-elements/Submit'
import InformationSecure from '../misc/InformationSecure'

interface PayFormInterface {
  Amount: string
  NameOnCard: string
  CardNumber: string
  CVV: string
  ExpirationMonth: string
  ExpirationYear: string
  ZipCode: string
  RoutingNumber: string
  bankName: string
  AccountNumber: string
  children?: JSX.Element | JSX.Element[]
}

const PayForm: React.FC = () => {
  const navigate = useNavigate()
  interface propState {
    token: string
    id: string
    dob: string
    balance: string
    mdvipId: string
  }

  const initialValues = {
    Amount: '',
    NameOnCard: '',
    CardNumber: '',
    CVV: '',
    ExpirationMonth: '',
    ExpirationYear: '',
    ZipCode: '',
    RoutingNumber: '',
    bankName: '',
    AccountNumber: '',
  }

  const [formValues, setFormValues] = useState(initialValues)
  const [formErrors, setFormErrors] = useState(initialValues)
  const [isSubmit, setIsSubmit] = useState(false)

  const $date = new Date()
  // Scheduled payment date is today.
  const currentDate = $date.toLocaleDateString('en-us', { year: 'numeric', month: 'numeric', day: 'numeric' })

  // Year dropdown formation.
  const year = $date.getFullYear()
  const [message, setMessage] = React.useState('')
  const years = Array.from(new Array(20), (label, value) => {
    const $year = value + year
    const obj: Option = { value: $year - 2000, label: $year.toString() }
    return obj
  })

  // Bank Payment Mutation.
  const [bankQuery] = useMutation(MUTATION_ACH, {
    fetchPolicy: 'no-cache',
  })
  // Credit Card Payment Mutation.
  const [cardQuery] = useMutation(MUTATION_CREDIT, {
    fetchPolicy: 'no-cache',
  })

  const location = useLocation()
  const { id, mdvipId, token, dob, balance } = location.state as propState

  /**
   * Payment information form submission.
   *
   * @param {object} e
   *  Event.
   */
  const onSubmit = (e: React.SyntheticEvent) => {
    e.preventDefault()

    setIsSubmit(false)
    setFormErrors(validate(formValues))
    setIsSubmit(true)

    if (isSubmit) {
      // Get Bank Values from the form.
      const Ach = e.target as typeof e.target & {
        AccountNumber: { value: string }
        RoutingNumber: { value: string }
      }

      // Amount to pay, Bank Name, is Checking or Saving account.
      const AmountBankBlock = e.target as typeof e.target & {
        Amount: { value: number }
        bankName: { value: number }
        Checking: { value: number }
      }

      // Get Credit card values from the form.
      const Card = e.target as typeof e.target & {
        CardNumber: { value: string }
        CVV: { value: string }
        ExpirationMonth: { value: string }
        ExpirationYear: { value: string }
        HSA: { value: string }
        NameOnCard: { value: string }
        ZipCode: { value: string }
      }

      const AccountNumber = Ach.AccountNumber !== undefined ? Ach.AccountNumber.value : ''
      const RoutingNumber = Ach.RoutingNumber !== undefined ? Ach.RoutingNumber.value : ''
      const Amount = AmountBankBlock.Amount !== undefined ? AmountBankBlock.Amount.value : 0
      const bankName = AmountBankBlock.bankName !== undefined ? AmountBankBlock.bankName.value : 0
      const Checking = AmountBankBlock.Checking !== undefined ? AmountBankBlock.Checking.value : 0

      const CardNumber = Card.CardNumber !== undefined ? Card.CardNumber.value : ''
      const CVV = Card.CVV !== undefined ? Card.CVV.value : ''
      const ExpirationMonth = Card.ExpirationMonth !== undefined ? Card.ExpirationMonth.value : ''
      const ExpirationYear = Card.ExpirationYear !== undefined ? Card.ExpirationYear.value : ''
      const NameOnCard = Card.NameOnCard !== undefined ? Card.NameOnCard.value : ''
      const ZipCode = Card.ZipCode !== undefined ? Card.ZipCode.value : ''
      const HSA = Card.HSA !== undefined ? 'Yes' : 'No'

      // If Bank Account form fields are filled.
      if (AccountNumber.length > 0 && RoutingNumber.length > 0) {
        const AccountType = Checking ? 'Checking' : 'Saving'
        const lastSixDigitsAccountNumber = AccountNumber.substring(AccountNumber.length - 6)
        const lastNineDigitsRoutingNumber = RoutingNumber.substring(RoutingNumber.length - 9)
        const paymentMethod = bankName + ' ' + AccountType + ' Account ending in ' + lastSixDigitsAccountNumber
        // Run query. If Bank is confirmed, go to review page.
        bankQuery({
          variables: { input: { AccountNumber: AccountNumber, RoutingNumber: RoutingNumber } },
          onCompleted(data) {
            navigate('/review', {
              state: {
                CreditCardInfo: null,
                ACHInfo: {
                  paymentType: data.verifyACHInformation.paymentType,
                  authTransactionId: data.verifyACHInformation.authTransactionId,
                  gatewayTokenId: data.verifyACHInformation.gatewayTokenId,
                  ACHBankName__c: bankName,
                  ACHLast6__c: lastSixDigitsAccountNumber,
                  ACHRoutingLast9__c: lastNineDigitsRoutingNumber,
                  ACH_Account_Type__c: AccountType,
                },
                token: token,
                amount: Amount,
                paymentMethod: paymentMethod,
                mdvipId: mdvipId,
                id: id,
                dob: dob,
                balance: balance,
                type: 'Bank',
              },
            })
          },
          onError(error) {
            console.log(error)
            setMessage('An Error occurred. Please try again later.')
          },
        })
      } else if (CardNumber.length > 0 && CVV.length > 0 && ExpirationMonth.length > 0 && ExpirationYear.length > 0) {
        // Credit card was selected for the payment
        const year = parseInt(ExpirationYear.slice(-2))
        const month = parseInt(ExpirationMonth)
        const lastFourDigitsCardNumber = CardNumber.substring(CardNumber.length - 4)
        // If Credit card form fields are filled.
        const paymentMethod = 'Credit Card ending in ' + CardNumber.substring(CardNumber.length - 4)

        cardQuery({
          variables: {
            input: { CardNumber: CardNumber, CVV: CVV, ExpirationMonth: month, ExpirationYear: year },
          },
          onCompleted(data) {
            navigate('/review', {
              state: {
                ACHInfo: null,
                CreditCardInfo: {
                  PaymentType__c: data.verifyCreditCardInformation.creditCardType,
                  gatewayTokenId: data.verifyCreditCardInformation.gatewayTokenId,
                  authTransactionId: data.verifyCreditCardInformation.authTransactionId,
                  CCLast4__c: lastFourDigitsCardNumber,
                  Name_on_Card__c: NameOnCard,
                  HSACard__c: HSA,
                  Expiration_Month__c: month,
                  Expiration_Year__c: ExpirationYear,
                },
                token: token,
                amount: Amount,
                paymentMethod: paymentMethod,
                id: id,
                dob: dob,
                mdvipId: mdvipId,
                balance: balance,
                type: 'Card',
                ZipCode: ZipCode,
              },
            })
          },
          onError(error) {
            console.log(error)
            setMessage('An Error occurred. Please try again later.')
          },
        }).then(function (data) {
          console.log('Data:', data)
        })
      } else {
        setMessage('The information you entered is not valid. Please double check it and try again.')
      }
    }
  }

  const handleOnChange = (e: any) => {
    const { name, value } = e.currentTarget
    setFormValues({ ...formValues, [name]: value })
  }

  const validate = (values: PayFormInterface) => {
    const errors: PayFormInterface = {
      Amount: '',
      NameOnCard: '',
      CardNumber: '',
      CVV: '',
      ExpirationMonth: '',
      ExpirationYear: '',
      ZipCode: '',
      RoutingNumber: '',
      bankName: '',
      AccountNumber: '',
    }

    if (parseInt(values.Amount) < 0) {
      errors.Amount = 'Amount should be positive number.'
    }
    if (parseInt(values.Amount) > 100000) {
      errors.Amount = [errors.Amount, 'Amount is to big for the online payment'].join(' ')
    }

    if (values.CardNumber.length > 0) {
      const ExpirationMonth = parseInt(values.ExpirationMonth)
      const ExpirationYear = parseInt(values.ExpirationYear)
      const CVV = values.CVV

      if (expiration.isPast(ExpirationMonth, ExpirationYear)) {
        errors.ExpirationMonth = 'This Card is expired'
      }

      if (!cvc.isValid(CVV)) {
        errors.CVV = 'The CVV is not valid.'
      }

      if (!card.isValid(values.CardNumber)) {
        errors.CardNumber = 'The Card number is not valid.'
      }

      const zipPattern = /(^\d{5}$)|(^\d{5}-\d{4}$)/
      if (!zipPattern.test(values.ZipCode)) {
        errors.ZipCode = 'Zip code format is invalid.'
      }
    } else {
      const routingValidation = routingNumber(values.RoutingNumber)
      const accountValidation = accountNumber(values.AccountNumber)

      if (!routingValidation.isValid) {
        errors.RoutingNumber = 'Routing Number is invalid'
      }

      if (!accountValidation.isValid) {
        errors.AccountNumber = 'Account Number is invalid'
      }
    }

    return errors
  }

  // Get bank name by routing number.
  const [isActive, setisActive] = React.useState('')
  const [bankName, setBankName] = React.useState('')

  const getBankName = (e: FocusEvent<HTMLInputElement>) => {
    const RoutingNumber = e.currentTarget.value
    if (RoutingNumber.length > 0) {
      fetch('https://www.routingnumbers.info/api/name.json?rn=' + RoutingNumber)
        .then((response) => response.json())
        .then((responseJson) => {
          setBankName(responseJson.name)
          setisActive('js-filled')
        })
        .catch((error) => {
          setMessage('The bank name was not found automatically please enter the bank name manually.')
        })
    }
  }

  return (
    <form className="pay-form" onSubmit={onSubmit}>
      <div className="amount-label">Enter the amount you wish to pay</div>
      <Input
        name="Amount"
        className={formErrors.Amount ? 'element-error js-filled' : formValues.Amount ? ' js-filled' : ''}
        onChange={handleOnChange}
        required={true}
        label="Payment Amount ($)"
        type="number"
        value={formValues.Amount}
      />
      <div className="input-description">
        Payment will be scheduled for {currentDate}. To schedule payment for a further date in the future, please{' '}
        <a href={MDVIP_LOGIN_LINK} target="_blank" rel="noreferrer noopener">
          login
        </a>{' '}
        to your account.
      </div>
      <hr />
      <InformationSecure />
      <div className="error">
        {message}
        {Object.keys(formErrors).map((key: string, index) => {
          return <div key={index}>{formErrors[key as keyof typeof formErrors]}</div>
        })}
      </div>
      <div className="options">
        <Tabs>
          <TabList>
            <Tab>
              <CheckIcon />
              Credit Card
            </Tab>
            <Tab>
              <CheckIcon />
              Bank Account
            </Tab>
          </TabList>
          <TabPanel>
            <Input
              name="NameOnCard"
              className={formErrors.NameOnCard ? 'element-error js-filled' : formValues.NameOnCard ? ' js-filled' : ''}
              onChange={handleOnChange}
              required={true}
              label="Name on card"
              type="text"
              value={formValues.NameOnCard}
            />
            <div className="d-flex card-number">
              <Input
                name="CardNumber"
                className={
                  formErrors.CardNumber ? 'element-error js-filled' : formValues.CardNumber ? ' js-filled' : ''
                }
                onChange={handleOnChange}
                required={true}
                label="Card Number"
                type="text"
                value={formValues.CardNumber}
              />
              <div className="card-icons">
                <img src="/assets/images/visa.svg" alt="Visa" />
                <img src="/assets/images/mastercard.svg" alt="Master Card" />
                <img src="/assets/images/american-express.svg" alt="American Express" />
                <img src="/assets/images/discover.svg" alt="Discover" />
              </div>
            </div>
            <div className="d-flex payment-code-expiration">
              <Input
                name="CVV"
                onChange={handleOnChange}
                required={true}
                label="Security Code"
                type="text"
                value={formValues.CVV}
                className={formErrors.CVV ? 'element-error js-filled' : formValues.CVV ? ' js-filled' : ''}
              />
              <Select
                name="ExpirationMonth"
                classNamePrefix="select"
                placeholder="Exp Date(MM)"
                className="select-field--wrapper"
                options={MONTHS}
              />
              <Select
                name="ExpirationYear"
                classNamePrefix="select"
                placeholder="Exp Date(YY)"
                className="select-field--wrapper"
                options={years}
              />
            </div>
            <div className="zip-code">
              <Input
                name="ZipCode"
                className={formErrors.ZipCode ? 'element-error js-filled' : formValues.ZipCode ? ' js-filled' : ''}
                onChange={handleOnChange}
                required={true}
                label="Zip Code"
                type="text"
                value={formValues.ZipCode}
              />
            </div>
            <div className="hsa-container">
              <div className="hsa-label">Health Saving Account</div>
              <Radio id="hsa" label="Yes" name="HSA" />
              <Radio id="hsa" label="No" name="HSA" />
            </div>
          </TabPanel>
          <TabPanel>
            <div className="bank-account-label pb-3">Bank Account Info</div>

            <div className="bank-account-type-label py-4">Account Type</div>

            <Tabs>
              <TabList>
                <Tab>
                  <CheckIcon />
                  Checking
                </Tab>
                <Tab>
                  <CheckIcon />
                  Saving
                </Tab>
              </TabList>

              <TabPanel>
                <div className="routing-number-disclaimer">Where to find routing and account number</div>
                <Input
                  name="RoutingNumber"
                  onChange={handleOnChange}
                  required={true}
                  label="Routing Number"
                  onBlur={getBankName}
                  type="text"
                  value={formValues.RoutingNumber}
                  className={
                    formErrors.RoutingNumber ? 'element-error js-filled' : formValues.RoutingNumber ? ' js-filled' : ''
                  }
                />
                <Input
                  name="bankName"
                  required={true}
                  label="Bank Name"
                  value={bankName}
                  type="text"
                  onChange={handleOnChange}
                  className={formErrors.bankName ? isActive + ' element-error js-filled' : isActive}
                />
                <Input
                  name="AccountNumber"
                  onChange={handleOnChange}
                  required={true}
                  label="Account Number"
                  type="text"
                  value={formValues.AccountNumber}
                  className={
                    formErrors.AccountNumber ? 'element-error js-filled' : formValues.AccountNumber ? ' js-filled' : ''
                  }
                />
                <Input name="Checking" label="" type="hidden" value="1" />
              </TabPanel>
              <TabPanel>
                <div className="routing-number-disclaimer">Where to find routing and account number</div>
                <Input
                  name="RoutingNumber"
                  onChange={handleOnChange}
                  required={true}
                  label="Routing Number"
                  onBlur={getBankName}
                  type="text"
                  value={formValues.RoutingNumber}
                  className={
                    formErrors.RoutingNumber ? 'element-error js-filled' : formValues.RoutingNumber ? ' js-filled' : ''
                  }
                />
                <Input
                  name="bankName"
                  onChange={handleOnChange}
                  required={true}
                  value={bankName}
                  label="Bank Name"
                  type="text"
                  className={formErrors.bankName ? isActive + ' element-error js-filled' : isActive}
                />
                <Input
                  name="AccountNumber"
                  onChange={handleOnChange}
                  required={true}
                  label="Account Number"
                  type="text"
                  className={
                    formErrors.AccountNumber ? 'element-error js-filled' : formValues.AccountNumber ? ' js-filled' : ''
                  }
                  value={formValues.AccountNumber}
                />
                <Input name="Checking" label="" type="hidden" value="0" />
                <div className="hsa-container">
                  <div className="hsa-label">Health Saving Account</div>
                  <Radio id="hsa" label="Yes" name="hsa" />
                  <Radio id="hsa" label="No" name="hsa" />
                </div>
              </TabPanel>
            </Tabs>
          </TabPanel>
        </Tabs>
      </div>
      <Submit id="payment-review" value="Review" onClick={onSubmit} />
    </form>
  )
}

export default PayForm
