import { ApolloClient, ApolloLink, ApolloProvider, createHttpLink, InMemoryCache } from '@apollo/client'
import KeyboardBackspaceIcon from '@mui/icons-material/KeyboardBackspace'
import { format } from 'date-fns'
import { useEffect } from 'react'
import { Link, useLocation } from 'react-router-dom'
import { Navigate } from 'react-router-dom'

import { MDVIP_MIDDLEWARE_URL } from '../Constants'
import PayForm from './Forms/PayForm'
import Navigation from './misc/Navigation'

interface propState {
  mdvipId: string
  balance: string
  dob: string
  token: string
  id: string
}

const VerifyAccount: React.FC = () => {
  useEffect(() => {
    document.title = 'MDVIP | Add Payment Info'
  })
  const today = new Date()
  const todayPlusOne = format(new Date(today.setDate(today.getDate() + 1)), 'MM/dd/yyyy')

  const location = useLocation()
  const { balance, dob, token, mdvipId, id } = location.state as propState

  const dobReformattedObject = new Date(dob)
  const dobReformatted =
    dobReformattedObject.getMonth() +
    1 +
    '/' +
    (dobReformattedObject.getDate() + 1) +
    '/' +
    dobReformattedObject.getFullYear()

  const httpLink = createHttpLink({
    uri: MDVIP_MIDDLEWARE_URL,
  })

  const authLink = new ApolloLink((operation, forward) => {
    operation.setContext({
      headers: {
        contentType: 'application/json',
        authorization: token ? `${token}` : '',
        channel: 'Express Pay',
        entityId: 'mdvip_connect_express_pay',
      },
    })
    return forward(operation)
  })
  //console.log(token)
  const client = new ApolloClient({
    link: authLink.concat(httpLink),
    cache: new InMemoryCache(),
  })
  return (
    <ApolloProvider client={client}>
      <div className="page-payment-info form-content">
        {id === '0' && <Navigate replace to="/" />}
        <Navigation activeIndex={1} />

        <div className="main-content">
          <div className="back-link">
            <KeyboardBackspaceIcon />
            <Link className="log-in" to="/">
              Verify Account
            </Link>
          </div>

          <h1>Add Payment Info</h1>
          <div className="content payment-info">
            <div className="member-info">
              <div className="table-row">
                <div className="table-column">
                  <div className="label">Member ID</div>
                  <div className="info">{mdvipId}</div>
                </div>
                <div className="table-column">
                  <div className="label">Date of Birth</div>
                  <div className="info">{dobReformatted}</div>
                </div>
              </div>
              <div className="table-row">
                <div className="table-column">
                  <div className="label">Amount Due</div>
                  <div className="info">${balance}</div>
                </div>
                <div className="table-column">
                  <div className="label">Date Billed</div>
                  <div className="info">{todayPlusOne}</div>
                </div>
              </div>
            </div>
            <div className="disclamer">
              Scheduled payments will be processed and reflected in your account in 1-2 days.
            </div>
            <hr />
            <div className="total-and-payment">
              <PayForm />
            </div>
          </div>
        </div>
      </div>
    </ApolloProvider>
  )
}

export default VerifyAccount
