/**
 * @file
 *
 * Review the information before payment form
 *
 * example:
 * <ReviewForm/>
 */

import { useMutation } from '@apollo/client'
import React from 'react'
import { useLocation, useNavigate } from 'react-router-dom'

import { MUTATION_CREATE_PAYMENT } from '../../Constants'
import Submit from '../form-elements/Submit'
import InformationSecure from '../misc/InformationSecure'

interface propState {
  amount: string
  ACHInfo: {
    achAccountType: string
    authTransactionId: string
    gatewayTokenId: string
    ACHBankName__c: string
    ACHLast6__c: string
    ACHRoutingLast9__c: string
    ACH_Account_Type__c: string
  }
  CreditCardInfo: {
    PaymentType__c: string
    gatewayTokenId: string
    authTransactionId: string
    CCLast4__c: string
    Name_on_Card__c: string
    HSACard__c: string
    Expiration_Month__c: string
    Expiration_Year__c: string
  }
  type: string
  ZipCode: string
}

const ReviewForm: React.FC = () => {
  const location = useLocation()
  const { amount, ACHInfo, CreditCardInfo, type, ZipCode } = location.state as propState
  const [message, setMessage] = React.useState('')
  const [createPayment] = useMutation(MUTATION_CREATE_PAYMENT, {
    fetchPolicy: 'no-cache',
  })
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const navigate = useNavigate()
  /**
   * Payment information form submission.
   *
   * @param e
   *  Event.
   */
  const onSubmit = (e: React.SyntheticEvent) => {
    e.preventDefault()

    let $input

    if (type == 'Card') {
      // Card Input
      $input = {
        ACHInfo: null,
        CreditCardInfo: {
          CCLast4__c: CreditCardInfo.CCLast4__c,
          Name_on_Card__c: CreditCardInfo.Name_on_Card__c,
          HSACard__c: CreditCardInfo.HSACard__c,
          Card_Type__c: 'COMMERCIAL',
          Expiration_Month__c: CreditCardInfo.Expiration_Month__c.toString(),
          Expiration_Year__c: '20' + CreditCardInfo.Expiration_Year__c,
          PaymentType__c: CreditCardInfo.PaymentType__c,
        },
        Active__c: true,
        Gateway_Token_Id__c: CreditCardInfo.gatewayTokenId,
        Auth_Transaction_Id__c: CreditCardInfo.authTransactionId,
        Source_URL__c: '',
        Web_MD_Domain__c: null,
        Affluence__c: null,
        Pre_Paid_Type__c: null,
        Address_1__c: null,
        Address_2__c: null,
        City__c: null,
        States__c: null,
        Zipcode__c: ZipCode,
      }
    } else {
      // Bank Input
      $input = {
        ACHInfo: {
          ACHBankName__c: ACHInfo.ACHBankName__c,
          ACHLast6__c: ACHInfo.ACHLast6__c,
          ACHRoutingLast9__c: ACHInfo.ACHRoutingLast9__c,
          ACH_Name_of_Account__c: ACHInfo.ACH_Account_Type__c,
        },
        CreditCardInfo: null,
        Active__c: true,
        Gateway_Token_Id__c: ACHInfo.gatewayTokenId,
        Auth_Transaction_Id__c: ACHInfo.authTransactionId,
        Source_URL__c: '',
        Web_MD_Domain__c: null,
        Affluence__c: null,
        Pre_Paid_Type__c: null,
        Address_1__c: null,
        Address_2__c: null,
        City__c: null,
        States__c: null,
        Zipcode__c: null,
      }
    }
    console.log($input)
    createPayment({
      variables: {
        input: $input,
      },
      onCompleted(data) {
        console.log(data)
        //navigate('/thank-you')
      },
      onError(error) {
        console.log(error)
        setMessage('An Error occurred. Please try again later.')
      },
    }).then(function (data) {
      console.log('Then DATA: ', data)
    })
  }

  return (
    <form className="review-form" onSubmit={onSubmit}>
      {message}
      <InformationSecure />
      <Submit id="payment-review" value={'Pay $' + amount} />
    </form>
  )
}

export default ReviewForm
